* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  text-align: center;
  background-color: #1e1e2e;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Arial, sans-serif;
  line-height: 1.6;
  display: flex;
}

button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(45deg, #ff6b6b, #f06595);
  border: none;
  border-radius: 8px;
  padding: 12px 24px;
  font-size: 18px;
  transition: all .3s ease-in-out;
}

button:hover {
  background: linear-gradient(45deg, #f06595, #ff6b6b);
  transform: scale(1.05);
}

input {
  color: #fff;
  text-align: center;
  background-color: #0000;
  border: 2px solid #ff6b6b;
  border-radius: 8px;
  padding: 12px;
  font-size: 18px;
}

input:focus {
  border-color: #f06595;
  outline: none;
}

.color-switcher {
  justify-content: center;
  align-items: center;
  gap: 15px;
  display: flex;
}

.timer-set {
  background: #ffffff1a;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 20px;
  display: flex;
}

.timer {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.field {
  flex-direction: column;
  display: flex;
}

.value {
  text-shadow: 2px 2px 10px #ffffff4d;
  font-size: 50px;
  font-weight: bold;
}

.arrow, .tusks {
  color: #fff;
  font-size: 32px;
  text-decoration: none;
  transition: color .3s ease-in-out;
}

.arrow:hover, .tusks:hover {
  color: #ff6b6b;
}

.list-of-tusk {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 80px;
  list-style: none;
  display: flex;
}
